<template>
  <div class="maintenance-page">
    <div class="flex justify-between items-center mb-6">
      <h1 class="text-2xl font-bold">维护记录管理</h1>
      <el-button type="primary" @click="showAddDialog = true">
        <el-icon class="mr-1"><Plus /></el-icon>
        添加维护记录
      </el-button>
    </div>

    <!-- 搜索和筛选 -->
    <div class="bg-white p-4 rounded-lg shadow mb-6">
      <div class="grid grid-cols-1 md:grid-cols-3 gap-4">
        <el-input
          v-model="searchQuery"
          placeholder="搜索车辆编号或维护类型"
          class="w-full"
        >
          <template #prefix>
            <el-icon><Search /></el-icon>
          </template>
        </el-input>
        
        <el-select v-model="statusFilter" placeholder="状态筛选" class="w-full">
          <el-option label="全部" value="" />
          <el-option label="计划中" value="planned" />
          <el-option label="进行中" value="in_progress" />
          <el-option label="已完成" value="completed" />
          <el-option label="已取消" value="cancelled" />
        </el-select>
        
        <el-date-picker
          v-model="dateRange"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          class="w-full"
        />
      </div>
    </div>

    <!-- 维护记录列表 -->
    <el-card shadow="hover">
      <el-table :data="filteredRecords" style="width: 100%">
        <el-table-column prop="id" label="记录ID" width="100" />
        <el-table-column prop="vehicleId" label="车辆编号" width="120">
          <template #default="{ row }">
            <span class="mr-1">🛴</span>
            {{ row.vehicleId }}
          </template>
        </el-table-column>
        <el-table-column prop="type" label="维护类型" width="140" />
        <el-table-column prop="description" label="描述" min-width="180" :show-overflow-tooltip="true" />
        <el-table-column prop="startDate" label="开始日期" width="180" />
        <el-table-column prop="endDate" label="完成日期" width="180" />
        <el-table-column prop="status" label="状态" width="120">
          <template #default="{ row }">
            <el-tag :type="getStatusType(row.status)">
              {{ getStatusText(row.status) }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="technician" label="维护人员" width="120" />
        <el-table-column label="操作" width="180" fixed="right">
          <template #default="{ row }">
            <el-button-group>
              <el-button size="small" @click="handleEdit(row)">
                编辑
              </el-button>
              <el-button 
                size="small" 
                type="success" 
                @click="handleComplete(row)"
                :disabled="row.status === 'completed' || row.status === 'cancelled'"
              >
                完成
              </el-button>
              <el-button 
                size="small" 
                type="danger" 
                @click="handleCancel(row)"
                :disabled="row.status === 'completed' || row.status === 'cancelled'"
              >
                取消
              </el-button>
            </el-button-group>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <div class="flex justify-end mt-4">
        <el-pagination
          v-model:current-page="currentPage"
          v-model:page-size="pageSize"
          :page-sizes="[10, 20, 50, 100]"
          :total="totalRecords"
          layout="total, sizes, prev, pager, next"
        />
      </div>
    </el-card>

    <!-- 添加/编辑维护记录对话框 -->
    <el-dialog
      v-model="showAddDialog"
      :title="editingRecord ? '编辑维护记录' : '添加维护记录'"
      width="600px"
    >
      <el-form
        ref="formRef"
        :model="recordForm"
        :rules="rules"
        label-width="100px"
      >
        <el-form-item label="车辆编号" prop="vehicleId">
          <el-select v-model="recordForm.vehicleId" placeholder="请选择车辆" filterable>
            <el-option 
              v-for="vehicle in vehicles" 
              :key="vehicle.id" 
              :label="vehicle.id" 
              :value="vehicle.id" 
            />
          </el-select>
        </el-form-item>
        
        <el-form-item label="维护类型" prop="type">
          <el-select v-model="recordForm.type" placeholder="请选择维护类型">
            <el-option label="日常检查" value="regular_check" />
            <el-option label="电池更换" value="battery_replacement" />
            <el-option label="轮胎维修" value="tire_repair" />
            <el-option label="制动系统" value="brake_system" />
            <el-option label="控制器维修" value="controller_repair" />
            <el-option label="全面检修" value="full_maintenance" />
          </el-select>
        </el-form-item>
        
        <el-form-item label="描述" prop="description">
          <el-input 
            v-model="recordForm.description" 
            type="textarea" 
            rows="3"
            placeholder="请输入维护详情描述"
          />
        </el-form-item>
        
        <el-form-item label="开始日期" prop="startDate">
          <el-date-picker
            v-model="recordForm.startDate"
            type="datetime"
            placeholder="选择开始日期时间"
            format="YYYY-MM-DD HH:mm"
            value-format="YYYY-MM-DD HH:mm"
          />
        </el-form-item>
        
        <el-form-item label="预计完成" prop="endDate">
          <el-date-picker
            v-model="recordForm.endDate"
            type="datetime"
            placeholder="选择预计完成日期时间"
            format="YYYY-MM-DD HH:mm"
            value-format="YYYY-MM-DD HH:mm"
          />
        </el-form-item>
        
        <el-form-item label="状态" prop="status">
          <el-select v-model="recordForm.status" placeholder="请选择状态">
            <el-option label="计划中" value="planned" />
            <el-option label="进行中" value="in_progress" />
            <el-option label="已完成" value="completed" />
            <el-option label="已取消" value="cancelled" />
          </el-select>
        </el-form-item>
        
        <el-form-item label="维护人员" prop="technician">
          <el-select v-model="recordForm.technician" placeholder="请选择维护人员">
            <el-option label="张工" value="张工" />
            <el-option label="李工" value="李工" />
            <el-option label="王工" value="王工" />
            <el-option label="赵工" value="赵工" />
          </el-select>
        </el-form-item>
        
        <el-form-item label="维护费用" prop="cost">
          <el-input-number
            v-model="recordForm.cost"
            :min="0"
            :precision="2"
            :step="10"
            class="w-full"
          />
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="showAddDialog = false">取消</el-button>
          <el-button type="primary" @click="handleSubmit">
            确定
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue'
import { Plus, Search } from '@element-plus/icons-vue'
import { ElMessage, ElMessageBox } from 'element-plus'

// 搜索和筛选
const searchQuery = ref('')
const statusFilter = ref('')
const dateRange = ref([])
const currentPage = ref(1)
const pageSize = ref(10)

// 表单相关
const showAddDialog = ref(false)
const editingRecord = ref(null)
const formRef = ref(null)

const recordForm = ref({
  id: '',
  vehicleId: '',
  type: '',
  description: '',
  startDate: '',
  endDate: '',
  status: 'planned',
  technician: '',
  cost: 0
})

const rules = {
  vehicleId: [
    { required: true, message: '请选择车辆', trigger: 'change' }
  ],
  type: [
    { required: true, message: '请选择维护类型', trigger: 'change' }
  ],
  description: [
    { required: true, message: '请输入描述', trigger: 'blur' }
  ],
  startDate: [
    { required: true, message: '请选择开始日期', trigger: 'change' }
  ],
  endDate: [
    { required: true, message: '请选择预计完成日期', trigger: 'change' }
  ],
  status: [
    { required: true, message: '请选择状态', trigger: 'change' }
  ],
  technician: [
    { required: true, message: '请选择维护人员', trigger: 'change' }
  ]
}

// 模拟数据
const maintenanceRecords = ref([
  {
    id: 'M001',
    vehicleId: 'A0001',
    type: '日常检查',
    description: '例行检查刹车系统和电池状态',
    startDate: '2024-03-15 09:00',
    endDate: '2024-03-15 11:00',
    status: 'completed',
    technician: '张工',
    cost: 50
  },
  {
    id: 'M002',
    vehicleId: 'B0002',
    type: '电池更换',
    description: '更换老化电池',
    startDate: '2024-03-18 14:00',
    endDate: '2024-03-18 16:00',
    status: 'completed',
    technician: '李工',
    cost: 300
  },
  {
    id: 'M003',
    vehicleId: 'C0003',
    type: '轮胎维修',
    description: '修复前轮漏气问题',
    startDate: '2024-03-20 10:00',
    endDate: '2024-03-20 12:00',
    status: 'in_progress',
    technician: '王工',
    cost: 80
  },
  {
    id: 'M004',
    vehicleId: 'A0005',
    type: '全面检修',
    description: '全车系统检修和清洁',
    startDate: '2024-03-25 09:00',
    endDate: '2024-03-25 17:00',
    status: 'planned',
    technician: '赵工',
    cost: 200
  }
])

// 模拟可选车辆数据
const vehicles = ref([
  { id: 'A0001', name: '电动滑板车 #A0001' },
  { id: 'B0002', name: '电动滑板车 #B0002' },
  { id: 'C0003', name: '电动滑板车 #C0003' },
  { id: 'A0004', name: '电动滑板车 #A0004' },
  { id: 'A0005', name: '电动滑板车 #A0005' },
  { id: 'B0006', name: '电动滑板车 #B0006' }
])

// 计算属性
const totalRecords = computed(() => maintenanceRecords.value.length)

const filteredRecords = computed(() => {
  return maintenanceRecords.value.filter(record => {
    // 搜索过滤
    const matchSearch = !searchQuery.value || 
      record.vehicleId.toLowerCase().includes(searchQuery.value.toLowerCase()) ||
      record.type.toLowerCase().includes(searchQuery.value.toLowerCase());
    
    // 状态过滤
    const matchStatus = !statusFilter.value || record.status === statusFilter.value;
    
    // 日期范围过滤
    let matchDate = true;
    if (dateRange.value && dateRange.value.length === 2) {
      const startDate = new Date(dateRange.value[0]);
      const endDate = new Date(dateRange.value[1]);
      const recordDate = new Date(record.startDate);
      
      matchDate = recordDate >= startDate && recordDate <= endDate;
    }
    
    return matchSearch && matchStatus && matchDate;
  });
})

// 状态相关方法
function getStatusType(status) {
  const types = {
    'planned': 'info',
    'in_progress': 'warning',
    'completed': 'success',
    'cancelled': 'danger'
  }
  return types[status] || 'info'
}

function getStatusText(status) {
  const texts = {
    'planned': '计划中',
    'in_progress': '进行中',
    'completed': '已完成',
    'cancelled': '已取消'
  }
  return texts[status] || status
}

// 操作方法
function handleEdit(row) {
  editingRecord.value = row
  recordForm.value = { ...row }
  showAddDialog.value = true
}

function handleComplete(row) {
  ElMessageBox.confirm(
    `确定将维护记录 ${row.id} 标记为已完成吗？`,
    '确认完成',
    {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'success'
    }
  ).then(() => {
    row.status = 'completed'
    row.endDate = new Date().toLocaleString('zh-CN', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit'
    }).replace(/\//g, '-')
    ElMessage.success('维护记录已标记为完成')
  })
}

function handleCancel(row) {
  ElMessageBox.confirm(
    `确定要取消维护记录 ${row.id} 吗？`,
    '确认取消',
    {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }
  ).then(() => {
    row.status = 'cancelled'
    ElMessage.success('维护记录已取消')
  })
}

async function handleSubmit() {
  if (!formRef.value) return
  
  await formRef.value.validate((valid) => {
    if (valid) {
      if (editingRecord.value) {
        // 更新现有记录
        const index = maintenanceRecords.value.findIndex(r => r.id === editingRecord.value.id)
        if (index > -1) {
          maintenanceRecords.value[index] = { ...recordForm.value }
        }
      } else {
        // 添加新记录
        const newId = 'M' + String(maintenanceRecords.value.length + 1).padStart(3, '0')
        maintenanceRecords.value.push({
          ...recordForm.value,
          id: newId
        })
      }
      
      showAddDialog.value = false
      editingRecord.value = null
      resetForm()
      ElMessage.success(editingRecord.value ? '更新成功' : '添加成功')
    }
  })
}

function resetForm() {
  recordForm.value = {
    id: '',
    vehicleId: '',
    type: '',
    description: '',
    startDate: '',
    endDate: '',
    status: 'planned',
    technician: '',
    cost: 0
  }
}
</script>

<style scoped>
.maintenance-page {
  padding: 20px;
}
</style> 